.floatwnd {
    display: none;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 99;
    text-align: left;
    outline: none;
}

.floatwnd.active {
    display: inline-block;
}

#note-contextmenu {
    background-color: rgba(0, 0, 0, 0.829);
    border: solid 2px rgb(255, 0, 0);
    width: 250px;
    /* height: 100px; */
}

#note-contextmenu>div {
    display: inline-block;
    width: 100%;
    height: 20px;
    /* margin-bottom: 2px; */
    /* background-color: rgba(127, 255, 212, 0.582); */
}

#note-contextmenu>div:hover {
    background-color: rgba(251, 255, 0, 0.185);
}

#note-contextmenu>div.disable {
    opacity: 0.5;
    pointer-events: none;
}

#note-contextmenu>div>span {
    display: inline-block;
    margin-top: 1px;
    font-weight: 600;
    font-size: 14px;
}

#note-contextmenu>div>.order {
    margin-left: 5px;
    color: red;
}

#note-contextmenu>div>.call {
    margin-left: 5px;
    color: rgb(255, 255, 255);
}

#note-contextmenu>div>.key {
    margin-left: 2px;
    color: rgba(255, 255, 255, 0.473);
}

#total-palette {
    background-color: rgba(0, 0, 0, 0.911);
    /* border: solid 2px rgb(0, 255, 13); */
    border: solid 2px rgb(207, 207, 207);
    width: 584px;
    /* height: 400px; */
}

#total-palette>*>div {
    display: inline-block;
    width: calc(100% - 10px);
    margin: 0 5px;
}
#total-palette>*>div.active {
    background-color: rgba(255, 38, 0, 0.116);
}

#total-palette>*>.chord-selector {
    /* background-color: rgba(255, 235, 205, 0.068); */
    /* height: 130px; */
}

#total-palette>*>.chord-selector>div {
    display: inline-block;
    width: 91px;
    height: 58px;
    margin: 2px;
    vertical-align: top;
}

#total-palette>*>.chord-selector>div>div {
    display: inline-block;
    text-align: center;
    width: 100%;
    height: 100%;
    background-color: rgba(50, 216, 238, 0.075);
    border: solid 1px rgba(255, 255, 255, 0.164);
}
#total-palette>*>.chord-selector>div>div:hover {
    background-color: rgba(255, 255, 255, 0.452);
}
#total-palette>*>.chord-selector>div>div.active {
    background-color: rgba(252, 227, 0, 0.623);
}
#total-palette>*>.chord-selector>div>.half {
    height: 27px;
}
#total-palette>*>.chord-selector>div>.half:nth-child(1) {
    margin-bottom: 4px;
}

#total-palette>*>.chord-selector>div>div>span {
    display: inline-block;
    color: rgba(255, 255, 255, 0.37);
    font-size: 16px;
    font-weight: 600;
    margin-top: 18px;
}
#total-palette>*>.chord-selector>div>.half>span {
    margin-top: 3px;
}
#total-palette>*>.chord-selector>div>div>.degree {
    color: rgba(255, 255, 255, 0.932);
}
#total-palette>*>.chord-selector>div>div>.chord {
    color: rgba(255, 255, 255, 0.555);
}


#total-palette>.symbol-selector {
    /* background-color: rgba(146, 206, 255, 0.068); */
    height: 180px;
    margin-top: 10px;
}
#total-palette>.symbol-selector>div:hover {
    background-color: rgba(255, 255, 255, 0.452);
}
#total-palette>.symbol-selector>div.active {
    background-color: rgba(252, 227, 0, 0.623);
}

#total-palette>.symbol-selector>div {
    display: inline-block;
    height: 28px;
    width: 91px;
    margin: 2px;
    vertical-align: top;
    background-color: rgba(238, 130, 238, 0.295);
    border: solid 1px rgba(255, 255, 255, 0.164);
}
#total-palette>.symbol-selector>div>span {
    display: inline-block;
    margin: 3px 7px 0 7px;
    font-size: 16px;
    font-weight: 600;
    color: yellow;
}

#total-palette>.beat-selector {
    /* background-color: rgba(255, 255, 255, 0.219); */
    margin: 0 0 0 6px;
    width: calc(100% - 12px);
    /* height: 60px; */
    margin-top: 10px;
}

#total-palette>.beat-selector>div {
    display: inline-block;
    width: 67px;
    height: 28px;
    margin: 2px;
    vertical-align: top;
    background-color: rgba(255, 255, 255, 0.123);
}
#total-palette>.beat-selector>div:hover {
    background-color: rgba(255, 255, 255, 0.336);
}
#total-palette>.beat-selector>div.active {
    background-color: rgba(255, 255, 255, 0.815);
}
#total-palette>.beat-selector>div.active:hover {
    background-color: rgba(255, 255, 255, 0.589);
}

#total-palette>.operation {
    background-color: rgba(146, 206, 255, 0.068);
    height: 30px;
    margin-top: 10px;
    text-align: right;
}
#total-palette>.operation>div {
    display: inline-block;
    width: 100px;
    height: 26px;
    background-color: rgba(255, 255, 255, 0.219);
    margin: 2px 5px 0 0;
    border: solid 1px white;
    text-align: center;
}
#total-palette>.operation>div:hover {
    background-color: rgba(255, 255, 255, 0.534);
}
#total-palette>.operation>div>span {
    display: inline-block;
    margin: 3px 7px 0 7px;
    font-size: 16px;
    font-weight: 600;
    color: rgb(255, 255, 255);
}