.note-element {
    display: inline-block;
    vertical-align: top;
    text-align: center;
    height: 150px;
    margin-bottom: 5px;
    transition: background-color 200ms;
}

/* 初期値アイテム */

.note-element.detail-init {
    /* margin: 50px 0 0 5px; */
    margin-left: 10px;
    width: 180px;
    padding: 10px;
    border: solid 2px rgba(0, 0, 0, 0.068);
    background-color: rgba(0, 0, 0, 0.02);
    border-radius: 3px;
    /* border-left: solid 2px rgba(41, 0, 95, 0.459);
    border-right: solid 2px rgba(41, 0, 95, 0.459); */
}

.note-element.detail-init>div:nth-child(1) {
    display: block;
    margin-top: 16px;
}

.note-element.detail-init>div:nth-child(1)>div {
    margin-top: 3px;
    display: block;
    position: relative;
    height: 20px;
    /* text-align: left; */
}

.note-element.detail-init>div:nth-child(1)>div.active {
    background-color: rgba(255, 136, 0, 0.473);
}

.note-element.detail-init>div:nth-child(1)>div>span {
    display: inline-block;
    position: absolute;
    top: 3px;
    font-size: 15px;
    font-weight: 600;
    /* background-color: rgba(238, 238, 238, 0.548); */
}

/* 初期化メニュータイトル */

.note-element.detail-init>div:nth-child(1)>div>span:nth-child(1) {
    left: 4px;
    color: rgba(0, 21, 61, 0.212);
}

/* 初期化メニュー値 */

.note-element.detail-init>div:nth-child(1)>div>span:nth-child(2) {
    left: 74px;
    color: rgba(0, 55, 156, 0.726);
}

.note-element.detail-chord {
    opacity: 0.4;
    margin-left: 5px;
    min-width: 100px;
    padding: 10px 20px;
}

.note-element.detail-chord:hover,
.note-element.detail-init:hover,
.note-element.detail-section:hover {
    border: solid 1px rgba(0, 0, 0, 0.2);
}

.note-element.detail-chord.current {
    opacity: 1;
}

.note-element.detail-chord>div {
    display: block;
    margin-top: 2px;
    background-color: rgba(0, 136, 255, 0.041);
}

/* ディグリー */
.note-element.detail-chord>.degree>span {
    font-size: 20px;
    font-weight: 600;
    color: rgba(204, 0, 61, 0.664);
}

.note-element.detail-chord>.degree.diatonic>span {
    color: rgba(0, 21, 61, 0.664);
}

.note-element.detail-chord>.degree.blank>span {
    color: rgba(0, 21, 61, 0.144);
}

/* 実コード */
.note-element.detail-chord>.chord>span {
    font-size: 15px;
    font-weight: 600;
    color: rgba(194, 62, 124, 0.664);
}

/* 持続時間チップ（4分の1） */

.note-element.detail-chord>.sustain>span {
    display: inline-block;
    width: 10px;
    height: 20px;
    background: rgba(49, 49, 49, 0.356);
    margin: 2px 1px;
}

.note-element.detail-fixed {
    margin-left: 10px;
    width: 100px;
    /* border: solid 1px rgba(0, 0, 0, 0.205); */
}

.note-element.detail-fixed>div {
    margin-top: 40px;
    font-size: 22px;
    color: rgba(58, 33, 39, 0.445);
    font-weight: 600;
    line-height: 60px;
}

.note-element.detail-section {
    width: 80px;
    height: 150px;
}

.note-element.detail-section>div:nth-child(1) {
    height: 30px;
    text-align: center;
    /* background-color: rgba(100, 148, 237, 0.13); */
}

.note-element.detail-section>div:nth-child(1)>span {
    display: inline-block;
    margin-top: 10px;
    font-size: 16px;
    color: black;
}
.note-element.detail-section.current>div:nth-child(1)>span {
    color: red;
    font-weight: 600;
}

.note-element.detail-section>div:nth-child(2) {
    display: inline-block;
    height: calc(100% - 40px);
    margin-left: 14;
    width: 4px;
    background-color: rgba(185, 1, 1, 0.479);
}
.note-element.detail-section.current>div:nth-child(2) {
    width: 6px;
    background-color: rgba(226, 26, 26, 0.76);
}

.note-element.active {
    background-color: rgba(0, 255, 0, 0.121);
    border: solid 1px rgba(0, 0, 0, 0.082);
}