#scorecont-notetab-root {
    display: inline-block;
    width: 100%;
    height: 100%;
    /* background-color: rgba(255, 127, 202, 0.664); */
    text-align: left;
}

#scorecont-notetab-root>div {
    display: inline-block;
    height: 100%;
    vertical-align: top;
}

/* 左側のコンポーネント */
#scorecont-notetab-root>.left-cont {
    display: inline-block;
    width: calc(100% - 223px);
}

/* スコアノートエリア */
#score-note {
    display: block;
    /* position: relative; */
    height: calc(100% - 150px);
    background-color: #eee;
    margin-bottom: 3px;
    overflow: hidden;
}

/* セクションバー */
#score-outline-root {
    /* display: inline-block; */
    margin-top: 5px;
    margin-left: 5px;
    margin-right: 5px;
    width: calc(100% - 10px);
    height: 50px;
    background-color: rgb(255, 255, 241);
    overflow: hidden;
    text-align: left;
}

#current-state {
    display: inline-block;
    width: 140px;
    height: 50px;
    /* background-color: aqua; */
    vertical-align: top;
}

#current-state>span {
    display: inline-block;
    margin-top: 8px;
    font-size: 18px;
    font-weight: 600;
    font-style: italic;
    font-family: Georgia, 'Times New Roman', Times, serif;
    color: rgba(20, 0, 110, 0.219);
}

#section-list {
    display: inline-block;
    margin-left: 5px;
    width: 90px;
    height: calc(100% - 60px);
    /* background-color: rgb(0, 255, 64); */
    background-color: rgb(255, 255, 241);
    vertical-align: top;
    text-align: center;
    border-right: solid 4px rgba(0, 0, 0, 0.048);
}

#section-list>div {
    display: inline-block;
    margin: 2px 1px;
    /* background: rgb(243, 232, 214); */
    /* border-radius: 3px; */
    border: solid 1px rgba(34, 34, 68, 0.267);
    width: 80px;
    height: 30px;
}

#section-list>div.active {
    /* background: rgba(248, 244, 8, 0.233); */
    background: rgba(248, 144, 8, 0.233);
}

#section-list>div>span {
    display: inline-block;
    font-size: 14px;
    color: rgba(34, 34, 68, 0.562);
    margin-top: 7px;
    /* font-weight: 600; */
}

#score-list-root {
    display: inline-block;
    margin-bottom: 5px;
    margin-right: 5px;
    width: calc(100% - 10px - 90px);
    height: calc(100% - 60px);
    /* height: 400px; */
    background-color: rgb(255, 255, 241);
    overflow-y: auto;
    text-align: left;
    outline: none;
}

#score-note.preview>div {
    background-color: rgb(235, 255, 241);
}

#score-footer {
    margin-top: 2px;
    display: block;
    height: 147px;
    width: 100%;
    background-color: #eeeeee;
    text-align: left;
}

#score-footer>.tab-list>.tab-menu {
    background-color: rgba(131, 128, 114, 0.603);
}

#score-footer>.tab-list>.tab-menu.tab-active {
    background-color: rgba(199, 195, 175, 0.671);
}

#score-footer>.cont-list {
    background-color: rgba(199, 195, 175, 0.671);
}


/* 右側のコンポーネント */
#scorecont-notetab-root>.right-cont {
    margin-left: 3px;
}

/* スコア詳細 */
#score-detail {
    display: inline-block;
    width: 220px;
    height: 100%;
    background-color: rgb(240, 232, 222);
    outline: none;
    overflow: auto;
}

#score-detail>div {
    display: inline-block;
    margin: 2px;
    width: calc(100% - 4px);
    height: calc(100% - 4px);
    background-color: rgba(255, 255, 255, 0.719);
}

#score-detail>div>.title {
    display: block;
    margin: 4px 4px 0 4px;
    width: calc(100% - 8px);
    height: 26px;
    /* background-color: rgba(0, 0, 0, 0.048); */
    /* border-bottom: solid 2px rgba(0, 0, 0, 0.158); */
}

#score-detail>div>.title>span {
    display: inline-block;
    margin: 3px 0 0 0;
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    color: rgba(192, 0, 0, 0.616);
}

#score-detail>div>.param-area {
    display: block;
    margin: 5px 4px 0 4px;
    width: calc(100% - 8px);
    height: 60px;
    background-color: rgba(212, 212, 212, 0.164);
    border-radius: 2px;
    border: solid 1px rgba(0, 0, 0, 0.089);
}

#score-detail>div>.param-area:hover {
    background-color: rgba(255, 255, 255, 0.144);
}

#score-detail>div>.param-area>.name {
    display: block;
    margin: 2px 0 4px 4px;
    width: calc(100% - 8px);
    height: 26px;
    /* background-color: rgba(0, 0, 255, 0.027); */
}
#score-detail>div>.param-area>.name>span {
    display: inline-block;
    margin: 3px 0 0 0;
    text-align: center;
    font-size: 17px;
    font-weight: 600;
    color: rgba(54, 33, 148, 0.521);
}

#score-detail>div>.param-area>.value {
    display: block;
    margin: 4px 0 4px 4px;
    width: calc(100% - 8px);
    height: 26px;
    /* background-color: rgba(255, 0, 34, 0.205); */
}
#score-detail>div>.param-area>.value>span {
    display: inline-block;
    margin: 3px 0 0 0;
    text-align: center;
    font-size: 17px;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.521);
}

#modal-blind {
    display: none;
    position: absolute;
    background-color: rgba(0, 11, 41, 0.685);
    left: 0;
    top: 0;
    z-index: 90;
    width: 100%;
    height: 100%;
    opacity: 0;
    /* transition: opacity, display 200ms; */
}

#modal-blind.active {
    display: block;
    opacity: 1;
}