.tab-list {
    display: inline-block;
    height: 30px;
    /* width: 100%; */
    font-size: 0;
    /* background-color: royalblue; */
}

.tab-menu {
    display: inline-block;
    /* width: 100px; */
    height: 100%;
    background-color: #99a;
    color: #555;
    opacity: 0.5;
    border-radius: 5px 5px 0 0;
    user-select: none;
    margin-right: 5px;
}

.tab-menu:hover {
    background-color: rgb(177, 177, 190);
}

.tab-menu>span {
    display: inline-block;
    margin-top: 7px;
    padding: 0 5px;
    font-size: 16px;
    font-weight: 600;
}

.tab-menu.active {
    opacity: 1;
    color: #fff;
    background-color: rgb(177, 177, 190);
    padding: 0 20px;
    transition: padding 100ms;
    user-select: none;
}

.cont-list {
    display: block;
    background-color: #99a;
    height: calc(100% - 30px);
    border-radius: 0 6px 6px 6px;
}

.tab-cont {
    display: none;
    /* background-color: rgba(0, 0, 0, 0.1); */
    height: calc(100% - 10px);
    width: calc(100% - 10px);
    margin: 5px;
    text-align: center;
    vertical-align: top;
}

.tab-cont.active {
    display: inline-block !important;
}

.disable {
    opacity: 0.3 !important;
    pointer-events: none !important;
    -webkit-user-select: none !important;
}

.hidden {
    display: none !important;
}