#scorecont-listtab-root {
    display: inline-block;
    width: 100%;
    height: 100%;
    /* background-color: rgba(255, 127, 202, 0.664); */
    text-align: left;
}

#scorecont-listtab-root>div {
    display: inline-block;
    height: 100%;
    vertical-align: top;
    background-color: aqua;
}

/* 左側のコンポーネント */
#scorecont-listtab-root>.left-cont {
    display: inline-block;
    width: calc(100% - 223px);
}
